<template>
    <div class='login'>
        <el-dialog :visible.sync="loginDialogVisible" width="20%" :before-close="handleClose" class="login-dialog">
            <div class="login-wrapper">
                <img src="../../assets/logo.jpg" alt="" class="login-logo">
                <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules">
                    <el-form-item prop="phone">
                        <el-input v-model="loginForm.phone" @change="refreshVerificationCode" placeholder="请输入帐号登录">
                            <i slot="prefix" class="iconfont icon-phone"></i>
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="pwd">
                        <el-input v-model="loginForm.pwd" placeholder="请输入密码" show-password>
                            <i slot="prefix" class="iconfont icon-pwd"></i>
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="pwd" style="display: flex;">
                        <el-input style="width: 68%;" v-model="loginForm.vercode" placeholder="请输入验证码" show-password>
                            <i slot="prefix" class="iconfont icon-pwd"></i>
                        </el-input>
                        <el-image style="width: 30%;height: 30px;top: 8px;" v-if="vercode" :src="vercode" title="点击更换验证码" @click="refreshVerificationCode"/>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitForm">登录</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
    components: {},
    data () {
        // 这里存放数据
        return {
            loginForm: {
                phone: '',
                pwd: '',
                vercode: ''
            },
            loginFormRules: {
                phone: [{ required: true, message: '请输入网易帐号', trigger: 'blur' }],
                pwd: [{ required: true, message: '请输入网易密码', trigger: 'blur' }]
            },
            // 验证码src
            vercode: null
        }
    },
    // 监听属性 类似于data概念
    computed: {
        ...mapGetters(['loginDialogVisible'])
    },
    // 方法集合
    methods: {
        ...mapMutations(['setLogin', 'setUserInfo', 'setLoginDialog']),
        handleClose () {
            this.setLoginDialog(false)
        },
        submitForm () {
            this.$refs.loginFormRef.validate(async (valid) => {
                if (valid) {
                    const { data: res } = await this.$http.login({
                        username: this.loginForm.phone,
                        password: this.loginForm.pwd,
                        vercode: this.loginForm.vercode
                    })
                    window.console.log('[信息]登录返回信息', res)
                    if (res.code !== 200) {
                        this.$msg.error(res.msg)
                    } else {
                        window.localStorage.setItem('LOGIN_RESULT', JSON.stringify(res.result))
                        window.localStorage.setItem('token', res.result.token)
                        window.sessionStorage.setItem('cookie', res.result.cookie)
                        window.localStorage.setItem('userId', res.result.loginCode)
                        this.getUserInfo(res.result)
                        this.setLoginDialog(false)
                        window.sessionStorage.setItem('isLogin', true)
                        this.$ichat.init()
                        window.console.log('[信息]登录保存信息', window.localStorage.userId, window.localStorage.token)
                    }
                }
            })
        },
        async getUserInfo (userInfo) {
            window.sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
            this.setLogin(true)
            this.setUserInfo(userInfo)
        },
        /**
         * 刷新二维码
         */
        refreshVerificationCode () {
            this.vercode = this.$http.getVercode(this.loginForm.phone)
        }
    }
}
</script>
<style scoped lang="less">
.login-logo {
    margin-bottom: 50px;
}
.login-dialog {

    /deep/.el-dialog__body {
        padding: 30px 20px 0;
    }

    .dialog-footer {
        position: relative;
        z-index: 2;
        display: block;
        width: 100%;
        margin-bottom: 100px;

        .el-button {
            width: 100%;
        }
    }

    /deep/.el-dialog__footer {
        position: relative;

        &:after {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            content: "";
            width: 100%;
            height: 100%;
            opacity: .3;
            background: url(../../assets/login_bg2.jpg) center bottom no-repeat;
            background-size: contain;
        }
    }
}

</style>
